<template>
  <div>
    <v-card class="mb-12" color="grey lighten-1" height="automatic">
      <v-app id="inspire">
        <h2>  ()</h2>
        <v-form>
          <v-container>
            <v-row>
              <v-col cols="2">
                <label>  <span>*</span></label>
              </v-col>
              <v-col cols="4">
                <v-text-field
                  v-model="newlandinfoAddress.ward_no"
                  class="custom-text-field"
                  label=""
                  height="10"
                  solo
                ></v-text-field>
              </v-col>
              <v-col cols="4">
                <span class="error-message" style="color: red" v-if="wardNoValidationMessage"
                  >Ward number is Required</span
                >
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="2">
                 
              </v-col>
              <v-col cols="4">
                <v-text-field
                  v-model="newlandinfoAddress.previous_address"
                  class="custom-text-field"
                  label=""
                  height="10"
                  solo
                ></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col md="2">
                <label> <span>*</span></label>
              </v-col>
              <v-col md="4">
                <v-text-field
                  v-model="newlandinfoAddress.tole_np"
                  class="custom-text-field"
                  label=""
                  height="10"
                  solo
                ></v-text-field>
              </v-col>
              <v-col cols="4">
                <span class="error-message" style="color: red" v-if="toleNpValidationMessage"
                  >Tole is required is Required</span
                >
              </v-col>
            </v-row>
            <v-row>
              <v-col md="2">
                <label> Tole<span>*</span></label>
              </v-col>
              <v-col md="4">
                <v-text-field
                  v-model="newlandinfoAddress.tole"
                  label=""
                  height="10"
                  class="custom-text-field"
                  solo
                ></v-text-field>
              </v-col>
              <v-col cols="4">
                <span class="error-message" style="color: red" v-if="toleValidationMessage"
                  >Tole is required is Required</span
                >
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="2">
                  
              </v-col>
              <v-col cols="4">
                <v-text-field
                  v-model="newlandinfoAddress.street_code_no"
                  class="custom-text-field"
                  label=""
                  height="10"
                  solo
                ></v-text-field>
              </v-col>
            </v-row>
            <span v-if="successfulMessage">
              <v-alert dense outlined type="alert">
                {{ successfulMessage }}
              </v-alert>
            </span>
          </v-container>
          <v-btn primary v-if="isPostAuthorized" v-on:click="validate" class="primary"
            >Submit</v-btn
          >
        </v-form>
        <br />
        <br />
        <v-simple-table class="registrationtable">
          <template v-slot:default>
            <thead>
              <tr class="registrationtable">
                <th class="text-left registrationtable">
                   
                </th>
                <th class="text-left registrationtable">
                  
                </th>
                <th class="text-left registrationtable">  </th>
                <th class="text-left registrationtable">Action</th>
              </tr>
            </thead>
            <tbody>
              <tr
                class="registrationtable"
                v-for="landinfoAddressList in landinfoAddress"
                :key="landinfoAddressList.id"
              >
                <td class="registrationtable">{{ landinfoAddressList.ward_no }}</td>
                <td class="registrationtable">
                  {{ landinfoAddressList.tole }}
                </td>
                <td>{{ landinfoAddressList.street_code_no }}</td>
                <td>
                  <v-btn
                    small
                    color="primary"
                    v-if="isPostAuthorized"
                    v-on:click="edit(landinfoAddressList.id)"
                    >Edit</v-btn
                  >
                  <v-btn
                    small
                    color="primary"
                    v-if="isPostAuthorized"
                    v-on:click="deleterecord(landinfoAddressList.id)"
                    >Delete</v-btn
                  >
                  <v-btn small color="primary" v-else v-on:click="edit(landinfoAddressList.id)"
                    >View</v-btn
                  >
                </td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
      </v-app>
    </v-card>
    <!-- <v-btn
      color="primary"
      @click="nextStep(5)"
    >
      Continue
    </v-btn>

    <v-btn text>Cancel</v-btn> -->
  </div>
</template>
<script>
import { VueAxios } from "@/plugins/axios";

export default {
  name: "Landinfoaddress",
  props: {
    nextStep: {
      type: Function
    },
    registrationId: {
      type: [String, Number]
    },
    id: {
      type: [String, Number]
    },
    application: {
      type: Object
    },
    isPostAuthorized: {
      type: Boolean
    }
  },

  data() {
    return {
      //   application: [],
      userRoleId: localStorage.getItem("auth_user_role_id"),
      date: "",
      landinfoAddress: [],
      newlandinfoAddress: {
        id: 0,
        reg_id: this.registrationId,
        validateis: true,
        ward_no: "",
        previous_address: "",
        street_code_no: "",
        tole: "",
        tole_np: ""
      },
      isValid: true,
      wardNoValidationMessage: false,
      toleNpValidationMessage: false,
      toleValidationMessage: false,
      successfulMessage: ""
    };
  },
  mounted() {
    const urlApp = `api/registration/${this.$route.params.id}`;
    VueAxios.get(urlApp).then(response => {
      //  this.application = response.data.data;
      console.log(response);
    });
    const url = `api/registration/${this.registrationId}/land-info-address`;
    VueAxios.get(url)
      .then(response => {
        // debugger;
        this.landinfoAddress = response.data.data;
        // this.newlandinfoAddress.clear();
      })
      .catch(error => {
        console.log(`api error:${error}`);
      });
  },
  methods: {
    validate() {
      const formObject = this;
      this.isValid = true;
      if (formObject.newlandinfoAddress.ward_no !== "") {
        this.wardNoValidationMessage = false;
        // this.save();
      } else {
        this.wardNoValidationMessage = true;
        this.isValid = false;
      }
      if (formObject.newlandinfoAddress.tole_np !== "") {
        this.toleNpValidationMessage = false;
        // this.save();
      } else {
        this.toleNpValidationMessage = true;
        this.isValid = false;
      }
      if (formObject.newlandinfoAddress.tole !== "") {
        this.toleValidationMessage = false;
        // this.save();
      } else {
        this.toleValidationMessage = true;
        this.isValid = false;
      }
      if (this.isValid) {
        this.save();
      }
    },
    clearAllData() {
      this.newlandinfoAddress.id = 0;
      this.newlandinfoAddress.reg_id = this.registrationId;
      this.newlandinfoAddress.ward_no = "";
      this.newlandinfoAddress.street_code_no = "";
      this.newlandinfoAddress.previous_address = "";
      this.newlandinfoAddress.tole = "";
      this.newlandinfoAddress.tole_np = "";
    },
    save() {
      //   debugger;
      if (
        this.newlandinfoAddress.id === "null" ||
        this.newlandinfoAddress.id === 0 ||
        this.newlandinfoAddress.id === undefined
      ) {
        const saveUrl = `/api/registration/${this.registrationId}/land-info-address`;
        VueAxios.post(saveUrl, this.newlandinfoAddress)
          .then(response => {
            // debugger;
            if (response.status === 200 || response.status === 201) {
              this.landinfoAddress.push(response.data.data);
              this.successfulMessage = "Record added successfully .";
              this.clearAllData();
            }
          })
          .catch(error => {
            // alert(2);
            console.log(`api error:${error}`);
          });
      } else {
        const eurl = `/api/registration/${this.$route.params.regId}/land-info-address/${this.newlandinfoAddress.id}`;
        VueAxios.put(
          eurl,
          this.newlandinfoAddress,
          {
            headers: {
              "X-Requested-With": "XMLHttpRequest",
              Authorization: `Bearer ${window.localStorage.getItem("token")}`
            },
            _method: "patch"
          },
          {}
        )
          .then(response => {
            // debugger;
            if (response.status === 200 || response.status === 201) {
              const i = this.landinfoAddress
                .map(item => item.id)
                .indexOf(this.newlandinfoAddress.id); // find index of your object
              this.landinfoAddress[i].ward_no = this.newlandinfoAddress.ward_no;
              this.landinfoAddress[i].street_code_no = this.newlandinfoAddress.street_code_no;
              this.landinfoAddress[i].previous_address = this.newlandinfoAddress.previous_address;
              this.landinfoAddress[i].tole = this.newlandinfoAddress.tole;
              this.landinfoAddress[i].tole_np = this.newlandinfoAddress.tole_np;
              this.successfulMessage = "Data updated successfully";
              this.clearAllData();
            }
          })
          .catch(error => {
            console.log(error);
          });
      }
    },
    edit(id) {
      //   debugger;
      //    const url = `api/registration/${this.$route.params.regId}/designer/${this.$route.params.id}`;
      const url = `api/registration/${this.$route.params.regId}/land-info-address/${id}`;
      VueAxios.get(url).then(response => {
        // debugger;
        this.newlandinfoAddress = response.data.data;
        console.log(response.data.data);
      });
    },
    deleterecord(id) {
      const deleteurl = `api/registration/${this.$route.params.regId}/land-info-address/${id}`;
      VueAxios.delete(deleteurl)
        .then(response => {
          // debugger;
          if (response.status === 200 || response.status === 201) {
            this.successfulMessage = "Data deleted successfully";
            const i = this.landinfoAddress.map(item => item.id).indexOf(id); // find index of your object
            this.landinfoAddress.splice(i, 1);
          }
          console.log(response);
        })
        .catch(error => {
          console.log(error);
        });
    }
  }
};
</script>

