                <th class="text-left registrationtable">Road width</th>
                <th class="text-left registrationtable"></th>
                <th class="text-left registrationtable">Action</th>
              </tr>
            </thead>
            <tbody>
              <tr
                class="registrationtable"
                v-for="charkillaList in charkilla"
                :key="charkillaList.id"
              >
                <td class="registrationtable">{{ charkillaList.direction }}</td>
                <td class="registrationtable">
                  {{ charkillaList.landscape_type }}
                </td>
