<template>
  <div>
    <v-card class="mb-12" color="grey lighten-1" height="auto">
      <v-app id="inspire">
        <h2> </h2>
        <v-form>
          <v-container>
            <v-row>
              <v-col cols="2">
                <label>  <span>*</span></label>
              </v-col>
              <v-col cols="4">
                <v-select
                  v-model="newapplicant.applicant_type_id"
                  class="custom-text-field v-text-field__details"
                  :items="options"
                  item-value="id"
                  item-text="value"
                  :key="id"
                  solo
                ></v-select>
              </v-col>
              <v-col cols="4">
                <span class="error-message" style="color: red" v-if="applicantTypeValidationMessage"
                  >Applicant Type is Required</span
                >
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="2">
                
              </v-col>
              <v-col cols="4">
                <v-select
                  v-model="houseownerId"
                  @change="houseownerDetail()"
                  :items="houseownerinfo"
                  item-value="id"
                  class="custom-text-field v-text-field__details"
                  item-text="name_np"
                  :key="id"
                  solo
                ></v-select>
              </v-col>
              <!-- </div> -->
            </v-row>
            <v-row>
              <v-col md="2">
                <label>   <span>*</span></label>
              </v-col>
              <v-col md="4">
                <v-select
                  class="custom-text-field v-text-field__details"
                  v-model="newapplicant.ho_applicant_relation"
                  :items="applicantRelations"
                  item-value="id"
                  item-text="value"
                  :key="id"
                  solo
                ></v-select>
              </v-col>
              <v-col cols="4">
                <span
                  class="error-message"
                  style="color: red"
                  v-if="applicantRelationValidationMessage"
                  >Applicant Relation is Required</span
                >
              </v-col>
            </v-row>
            <v-row>
              <v-col md="2">
                <label>  /  <span>*</span></label>
              </v-col>
              <v-col md="4">
                <!-- <v-text-field v-model="newapplicant.salutation" label="" height="10" solo class="custom-text-field v-text-field__details"></v-text-field> -->
                <v-select
                  v-model="newapplicant.salutation"
                  :items="salutation"
                  item-value="id"
                  class="custom-text-field"
                  item-text="value"
                  :key="id"
                  solo
                ></v-select>
              </v-col>
              <v-col cols="4">
                <span class="error-message" style="color: red" v-if="salutationValidationMessage"
                  >Salutation is Required</span
                >
              </v-col>
            </v-row>
            <v-row>
              <v-col md="2">
                 
              </v-col>
              <v-col md="4">
                <v-text-field
                  v-model="newapplicant.nominee_name"
                  label=""
                  height="10"
                  solo
                  class="custom-text-field v-text-field__details"
                ></v-text-field>
              </v-col>
              <v-col md="2"> </v-col>
              <v-col md="4">
                <v-text-field
                  v-model="newapplicant.nominee_name_np"
                  label=""
                  height="10"
                  solo
                  class="custom-text-field v-text-field__details"
                ></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col md="2">
                 
              </v-col>
              <v-col md="4">
                <v-text-field
                  v-model="newapplicant.applicant_name"
                  label=""
                  height="10"
                  solo
                  class="custom-text-field v-text-field__details"
                ></v-text-field>
              </v-col>
              <v-col md="2"> </v-col>
              <v-col md="4">
                <v-text-field
                  v-model="newapplicant.applicant_name_np"
                  label=""
                  height="10"
                  solo
                  class="custom-text-field v-text-field__details"
                ></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col md="2">
                
              </v-col>
              <v-col md="4">
                <!-- <v-text-field v-model="newapplicant.citizenship_district_id" label="" height="10" solo></v-text-field> -->
                <v-select
                  :items="landownerinfo"
                  item-value="id"
                  item-text="landowner_name"
                  :key="id"
                  solo
                  class="custom-text-field v-text-field__details"
                ></v-select>
              </v-col>
              <v-col md="2">
                 
              </v-col>
              <v-col md="4">
                <v-text-field
                  v-model="newapplicant.citizenship_no"
                  label=""
                  height="10"
                  solo
                  class="custom-text-field v-text-field__details"
                ></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col md="2">
                  
              </v-col>
              <v-col md="4">
                <!-- <v-text-field v-model="newapplicant.citizenship_issue_district_id " label="" height="10" solo></v-text-field> -->
                <v-select
                  v-model="newapplicant.citizenship_issue_district_id"
                  :items="districts"
                  item-value="id"
                  item-text="district_name"
                  :key="id"
                  solo
                ></v-select>
              </v-col>
              <v-col md="2">
                 
              </v-col>
              <v-col cols="12" md="4">
                <v-text-field v-model="newapplicant.phone_no" label="" required></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col md="2">
                
              </v-col>
              <v-col cols="12" md="4">
                <input type="file" id="file-upload" @change="onFileChange" />
                {{ this.newapplicant.photo }}
              </v-col>
              <!-- <v-col md="2">
                                Ward no
                            </v-col>
                            <v-col cols="12" md="4">
                                <v-text-field v-model="newapplicant.ward_no" label="" required></v-text-field>
                            </v-col> -->
            </v-row>
            <span v-if="successfulMessage">
              <v-alert dense outlined type="alert">
                {{ successfulMessage }}
              </v-alert>
            </span>
          </v-container>
          <v-btn primary v-if="isPostAuthorized" v-on:click="validate" class="primary"
            >Submit</v-btn
          >
        </v-form>
        <v-simple-table class="registrationtable">
          <template v-slot:default>
            <thead>
              <tr class="registrationtable">
                <th class="text-left registrationtable">
                   
                </th>
                <!-- <th class="text-left registrationtable">
                                   Ward no
                                </th> -->
                <th class="text-left registrationtable"> </th>
                <th class="text-left registrationtable"> </th>
                <th class="text-left registrationtable">Action</th>
              </tr>
            </thead>
            <tbody>
              <tr
                class="registrationtable"
                v-for="applicantList in applicant"
                :key="applicantList.id"
              >
                <td class="registrationtable">{{ applicantList.nominee_name_np }}</td>
                <!-- <td class="registrationtable">
                                    {{ applicantList.ward_no }}
                                </td> -->
                <td>{{ applicantList.citizenship_no }}</td>
                <td>{{ applicantList.phone_no }}</td>
                <td>
                  <v-btn
                    small
                    color="primary"
                    v-if="isPostAuthorized"
                    v-on:click="edit(applicantList.id)"
                    >Edit</v-btn
                  >
                  <v-btn
                    small
                    color="primary"
                    v-if="isPostAuthorized"
                    v-on:click="deleterecord(applicantList.id)"
                    >Delete</v-btn
                  >
                  <v-btn small color="primary" v-else v-on:click="edit(applicantList.id)"
                    >View</v-btn
                  >
                </td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
      </v-app>
    </v-card>
  </div>
</template>
<script>
import { VueAxios } from "@/plugins/axios";

export default {
  name: "Applicant",
  props: {
    nextStep: {
      type: Function
    },
    registrationId: {
      type: [String, Number]
    },
    id: {
      type: [String, Number]
    },
    application: {
      type: Object
    },
    isPostAuthorized: {
      type: Boolean
    }
  },

  data() {
    return {
      houseownerinfo: [],
      districts: [],
      applicantTypeValidationMessage: false,
      applicantRelationValidationMessage: false,
      salutationValidationMessage: false,
      landownerinfo: [],
      salutation: [
        { id: "1", value: "Mr" },
        { id: "2", value: "Mrs" }
      ],
      date: "",
      options: [
        { id: "1", value: "" },
        { id: "2", value: "" },

        { id: "3", value: "" }
      ],
      applicantRelations: [
        { id: "1", value: "/" },
        { id: "2", value: "/" },

        { id: "3", value: "" },
        { id: "4", value: " " }
      ],
      houseownerId: 0,
      applicant: [],
      successfulMessage: "",
      userRoleId: localStorage.getItem("auth_user_role_id"),
      validateis: true,
      newapplicant: {
        id: 0,
        reg_id: this.registrationId,
        applicant_type_id: "",
        salutation: "",
        ho_applicant_relation: "",
        nominee_name: "",
        nominee_name_np: "",
        applicant_name: "",
        applicant_name_np: "",
        citizenship_no: "",
        citizenship_issue_district_id: "",
        phone_no: "",
        photo: "",
        ward_no: ""
      },
      formData: new FormData()
    };
  },
  mounted() {
    const url = `api/registration/${this.registrationId}/applicant`;
    VueAxios.get(url)
      .then(response => {
        // debugger;
        this.applicant = response.data.data;
        // this.newapplicant.clear();
      })
      .catch(error => {
        console.log(`api error:${error}`);
      });
    const houseownerinfoUrl = `api/registration/${this.registrationId}/houseowner-detail`;
    VueAxios.get(houseownerinfoUrl)
      .then(response => {
        // debugger;
        this.houseownerinfo = response.data.data;
        console.log(this.houseownerinfo);
        //   this.newhouseownerinfo.clear();
      })
      .catch(error => {
        console.log(`api error:${error}`);
      });
    const landownerinfoUrl = `api/registration/${this.registrationId}/landowner-info`;
    VueAxios.get(landownerinfoUrl)
      .then(response => {
        // debugger;
        this.landownerinfo = response.data.data;
        console.log(this.landownerinfo);
        //   this.newhouseownerinfo.clear();
      })
      .catch(error => {
        console.log(`api error:${error}`);
      });
    const districtUrl = "api/districts";
    VueAxios.get(districtUrl)
      .then(response => {
        // debugger;
        this.districts = response.data.data;
      })
      .catch(error => {
        console.log(`api error:${error}`);
      });
  },
  methods: {
    validate() {
      const formObject = this;
      this.isValid = true;
      if (formObject.newapplicant.applicant_type_id !== "") {
        this.applicantTypeValidationMessage = false;
        // this.save();
      } else {
        this.applicantTypeValidationMessage = true;
        this.isValid = false;
      }
      if (formObject.newapplicant.ho_applicant_relation > 0) {
        this.applicantRelationValidationMessage = false;
        // this.save();
      } else {
        this.applicantRelationValidationMessage = true;
        this.isValid = false;
      }
      if (formObject.newapplicant.salutation > 0) {
        this.salutationValidationMessage = false;
        // this.save();
      } else {
        this.salutationValidationMessage = true;
        this.isValid = false;
      }
      if (this.isValid) {
        this.save();
      }
    },
    scrollToTop() {
      window.scrollTo(0, 0);
    },
    houseownerDetail() {
      console.log(this.houseownerId);
      const url = `api/registration/${this.registrationId}/houseowner-detail/${this.houseownerId}`;
      VueAxios.get(url).then(response => {
        // debugger;
        this.newapplicant.citizenship_no = response.data.data.citizenship_no;
        this.newapplicant.phone_no = response.data.data.phone_no;
        this.newapplicant.applicant_name_np = response.data.data.name_np;
        this.newapplicant.applicant_name = response.data.data.name;
        this.newapplicant.ward_no = response.data.data.ward_no;
      });
    },
    clearAllData() {
      this.newapplicant.id = 0;
      this.newapplicant.reg_id = this.registrationId;
      this.newapplicant.applicant_type_id = "";
      this.newapplicant.salutation = "";
      this.newapplicant.ho_applicant_relation = "";
      this.newapplicant.nominee_name = "";
      this.newapplicant.nominee_name_np = "";
      this.newapplicant.applicant_name = "";
      this.newapplicant.applicant_name_np = "";
      this.newapplicant.citizenship_no = "";
      this.newapplicant.citizenship_issue_district_id = "";
      this.newapplicant.phone_no = "";
      this.newapplicant.photo = "";
      this.newapplicant.ward_no = "";
    },
    onFileChange(e) {
      const self = this;

      const files = e.target.files || e.dataTransfer.files;
      console.log(files[0].size);
      if (files.length > 0 && files[0].size < 1024 * 1024 * 5) {
        for (let i = 0; i < files.length; i += 1) {
          self.formData.append("photo", files[i], files[i].name);
        }
      } else {
        document.getElementById("file-upload").value = "";
        alert("file greater than 5mb");
      }
    },
    save() {
      const self = this;
      self.formData.append("applicant_type_id", this.newapplicant.applicant_type_id);
      self.formData.append("salutation", this.newapplicant.salutation);
      self.formData.append("ho_applicant_relation", this.newapplicant.ho_applicant_relation);
      self.formData.append("nominee_name", this.newapplicant.nominee_name);
      self.formData.append("nominee_name_np", this.newapplicant.nominee_name_np);
      self.formData.append("applicant_name", this.newapplicant.applicant_name);
      self.formData.append("applicant_name_np", this.newapplicant.applicant_name_np);
      self.formData.append("citizenship_no", this.newapplicant.citizenship_no);
      self.formData.append(
        "citizenship_issue_district_id",
        this.newapplicant.citizenship_issue_district_id
      );
      self.formData.append("phone_no", this.newapplicant.phone_no);
      self.formData.append("reg_id", this.newapplicant.reg_id);
      self.formData.append("ward_no", this.newapplicant.ward_no);
      if (
        this.newapplicant.id === "null" ||
        this.newapplicant.id === 0 ||
        this.newapplicant.id === undefined
      ) {
        const saveUrl = `/api/registration/${this.registrationId}/applicant`;
        VueAxios.post(saveUrl, self.formData)
          .then(response => {
            // debugger;
            if (response.status === 200 || response.status === 201) {
              this.applicant.push(response.data.data);
              this.successfulMessage = "Record added successfuly.";
              this.clearAllData();
            }
          })
          .catch(error => {
            // alert(2);
            console.log(`api error:${error}`);
          });
      } else {
        const eurl = `/api/registration/${this.$route.params.regId}/applicant/${this.newapplicant.id}`;
        VueAxios.put(
          eurl,
          self.formData,
          {
            headers: {
              "X-Requested-With": "XMLHttpRequest",
              Authorization: `Bearer ${window.localStorage.getItem("token")}`
            },
            _method: "patch"
          },
          {}
        )
          .then(response => {
            // debugger;
            if (response.status === 200 || response.status === 201) {
              const i = this.applicant.map(item => item.id).indexOf(this.newapplicant.id); // find index of your object
              this.applicant[i].nominee_name_np = this.newapplicant.nominee_name_np;
              // this.applicant[i].ward_no = this.newapplicant.ward_no;
              this.applicant[i].citizenship_no = this.newapplicant.citizenship_no;
              this.applicant[i].phone_no = this.newapplicant.phone_no;
              this.successfulMessage = "Data updated successfully";
              this.successfulMessage = "Data updated successfully";
              this.clearAllData();
            }
          })
          .catch(error => {
            console.log(error);
          });
      }
    },
    edit(id) {
      //   debugger;
      //    const url = `api/registration/${this.$route.params.regId}/applicant/${this.$route.params.id}`;
      const url = `api/registration/${this.$route.params.regId}/applicant/${id}`;
      VueAxios.get(url).then(response => {
        // debugger;
        this.newapplicant = response.data.data;
      });
    },
    deleterecord(id) {
      const deleteurl = `api/registration/${this.$route.params.regId}/applicant/${id}`;
      VueAxios.delete(deleteurl)
        .then(response => {
          // debugger;
          if (response.status === 200 || response.status === 201) {
            this.successfulMessage = "Data deleted successfully";
            const i = this.applicant.map(item => item.id).indexOf(id); // find index of your object
            this.applicant.splice(i, 1);
          }
          console.log(response);
        })
        .catch(error => {
          console.log(error);
        });
    }
  }
};
</script>
