<template>
  <div class="vld-parent">
    <loading :active.sync="isLoading" :can-cancel="true" :is-full-page="true"></loading>

    <v-card class="mb-12" color="grey lighten-1">
      <v-app id="inspire">
        <v-container>
          <h2></h2>
          <v-form>
            <v-row>
              <v-col md="4">
                <label>  (..)<span>*</span></label>
                <!-- {{id}} -->
              </v-col>
              <v-col cols="4">
                <v-text-field
                  v-model="application.application_date_np"
                  height="10"
                  solo
                  class=" "
                  ref="dateField"
                ></v-text-field>
              </v-col>
              <v-col cols="4">
                <span
                  class="error-message"
                  style="color: red"
                  v-if="applicationDateNpValidationMessage"
                  >Date is Required</span
                >
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="4">
                <label>  <span>*</span></label>
              </v-col>

              <v-col cols="4">
                <!-- <v-text-field v-model="application.building_use_id" label="" height="10" solo></v-text-field> -->

                <v-select
                  v-model="application.building_use_id"
                  :items="buildingUse"
                  item-value="id"
                  item-text="building_use_type"
                  :key="id"
                  solo
                ></v-select>
              </v-col>
              <v-col cols="4">
                <span class="error-message" style="color: red" v-if="buildingUseValidationMessage"
                  >Must select an option</span
                >
              </v-col>
            </v-row>
            <v-row>
              <v-col md="4">
                <label>     <span>*</span></label>
              </v-col>

              <v-col md="4">
                <!-- <v-text-field v-model="application.building_category_id" label="" height="10" solo></v-text-field>
                                 -->
                <v-select
                  required
                  v-model="application.building_category_id"
                  :items="buildingCategory"
                  item-value="id"
                  item-text="building_category_type"
                  :key="id"
                  solo
                ></v-select>
              </v-col>
              <v-col cols="4">
                <span
                  class="error-message"
                  style="color: red"
                  v-if="buildingCategoryValidationMessage"
                  >Must select an option</span
                >
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="12" md="4">
                <label>   <span>*</span></label>
              </v-col>

              <v-col md="4">
                <v-select
                  required
                  v-model="application.building_structure_id"
                  :items="buildingStructure"
                  item-value="id"
                  item-text="building_structure_type"
                  :key="id"
                  solo
                ></v-select>
              </v-col>
              <v-col cols="4">
                <span
                  class="error-message"
                  style="color: red"
                  v-if="buildingStructureValidationMessage"
                  >Structure is required</span
                >
              </v-col>
            </v-row>
            <v-row>
              <v-col md="4">
                <label>  <span>*</span></label>
              </v-col>

              <v-col md="2">
                <v-text-field
                  required
                  v-model="application.building_length_ft"
                  label="feet"
                  height="10"
                  solo
                ></v-text-field>
              </v-col>
              <v-col md="2">
                <v-text-field
                  required
                  v-model="application.building_length_in"
                  label="inch"
                  height="10"
                  solo
                ></v-text-field>
              </v-col>
              <v-col cols="4">
                <span
                  class="error-message"
                  style="color: red"
                  v-if="buildingLengthValidationMessage"
                  >Length is required</span
                >
              </v-col>
            </v-row>
            <v-row>
              <v-col md="4">
                <label>  <span>*</span></label>
              </v-col>

              <v-col md="2">
                <v-text-field
                  required
                  v-model="application.building_breadth_ft"
                  label="feet"
                  height="10"
                  solo
                ></v-text-field>
              </v-col>
              <v-col md="2">
                <v-text-field
                  required
                  v-model="application.building_breadth_in"
                  label="inch"
                  height="10"
                  solo
                ></v-text-field>
              </v-col>
              <v-col cols="4">
                <span
                  class="error-message"
                  style="color: red"
                  v-if="buildingBreadthValidationMessage"
                  >Breadth is required</span
                >
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="12" md="4">
                <label>   <span>*</span></label>
              </v-col>

              <v-col cols="12" md="2">
                <v-text-field
                  required
                  v-model="application.building_height_ft"
                  label="feet"
                  solo
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="2">
                <v-text-field
                  required
                  v-model="application.building_height_in"
                  label="inch"
                  height="10"
                  solo
                ></v-text-field>
              </v-col>
              <v-col cols="4">
                <span
                  class="error-message"
                  style="color: red"
                  v-if="buildingHeightValidationMessage"
                  >Height is required</span
                >
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="12" md="4">
                <label>  <span>*</span></label>
              </v-col>

              <v-col cols="12" md="4">
                <v-text-field v-model="application.building_storey" label="" solo></v-text-field>
              </v-col>
              <v-col cols="4">
                <span
                  class="error-message"
                  style="color: red"
                  v-if="buildingStoreyValidationMessage"
                  >Storey is required</span
                >
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="12" md="4">
                <label> Total Area (Total Building Floor Area)<span>*</span></label>
              </v-col>
              <!-- <v-text-field v-model="application.reg_id" :value="applicationrole.id" type="hidden" ></v-text-field> -->
              <v-col cols="12" md="4">
                <v-text-field
                  required
                  v-model="application.building_max_area"
                  label=""
                  solo
                ></v-text-field>
              </v-col>
              <v-col cols="4">
                <span class="error-message" style="color: red" v-if="buildingAreaValidationMessage"
                  >Area is required</span
                >
              </v-col>
            </v-row>
            <v-row>
              <v-col>
                <v-btn primary v-on:click="validate" v-if="isPostAuthorized" class="primary"
                  >Submit</v-btn
                >
              </v-col>
            </v-row>
            <v-row></v-row>
          </v-form>

          <span v-if="successfulMessage">
            <v-alert dense outlined type="alert">
              {{ successfulMessage }}
            </v-alert>
          </span>
        </v-container>
      </v-app>
    </v-card>
  </div>
</template>

<script>
import { VueAxios } from "@/plugins/axios";
import * as nepaliDatePicker from "../../../../node_modules/nepali-date-picker/dist/jquery.nepaliDatePicker.min.js"; /*eslint-disable-line*/
import "../../../../node_modules/nepali-date-picker/dist/nepaliDatePicker.min.css";

export default {
  name: "Application",
  props: {
    nextStep: {
      type: Function
    },
    registrationId: {
      type: [String, Number]
    },
    id: {
      type: [String, Number]
    },
    isPostAuthorized: {
      type: Boolean
    }
  },

  data() {
    return {
      date: "",
      isValid: true,
      validateis: true,
      userRoleId: localStorage.getItem("auth_user_role_id"),
      applicationDateNpValidationMessage: false,
      buildingUseValidationMessage: false,
      buildingCategoryValidationMessage: false,
      buildingStructureValidationMessage: false,
      buildingLengthValidationMessage: false,
      buildingBreadthValidationMessage: false,
      buildingHeightValidationMessage: false,
      buildingStoreyValidationMessage: false,
      buildingAreaValidationMessage: false,
      buildingUse: [],
      buildingCategory: [],
      buildingStructure: [],
      application: {
        id: 0,
        // reg_id: 0,
        application_date: "2020-04-22",
        application_date_np: "",
        building_category_id: "",
        building_use_id: "",
        building_structure_id: "",
        building_length_in: "",
        building_breadth_in: "",
        building_height_in: "",
        building_length_ft: "",
        building_breadth_ft: "",
        building_height_ft: "",
        building_storey: "",
        building_max_area: ""
      },
      applicationrole: [],
      // applicationList: [],
      successfulMessage: ""
    };
  },
  computed: {
    isLoading() {
      if (
        this.buildingUse.length === 0 ||
        this.buildingCategory.length === 0 ||
        this.buildingStructure.length === 0
      ) {
        // all drop-down values not initiated from api
        return true;
      }
      return false;
    }
  },
  created() {
    this.buildingUseCall();
    this.buildingCategoryCall();
    this.buildingStructureCall();
    this.applicationDetailCall();
    this.getRole();
  },
  mounted() {
    const { dateField } = this.$refs;
    if (dateField) {
      $(dateField.$el).nepaliDatePicker({
        dateFormat: "%y-%m-%d",
        closeOnDateSelect: true
      });
      $(dateField.$el).on("dateChange", event => {
        this.application.application_date_np = event.datePickerData.formattedDate;
      });
    }
  },
  methods: {
    getRole() {
      const urlApp = `api/registration/${this.$route.params.regId}`;
      VueAxios.get(urlApp, {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`
        }
      }).then(response => {
        //    debugger;
        this.applicationrole = response.data.data;
        console.log(this.applicationrole.phase_id);
        if (this.applicationrole.desk_user_role_id === this.userRoleId) {
          this.validateis = false;
        } else {
          this.validateis = true;
        }

        console.log(this.userRoleId);
        console.log(this.applicationrole.desk_user_role_id);
      });
    },
    applicationDetailCall() {
      const url = `api/registration/${this.registrationId}/application`;
      VueAxios.get(url, {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`
        }
      })
        .then(response => {
          // debugger;
          this.application = response.data.data;
        })
        .catch(error => {
          console.log(`api error:${error}`);
        });
    },
    buildingUseCall() {
      const buildingUseUrl = "api/building-use";
      VueAxios.get(buildingUseUrl)
        .then(response => {
          this.buildingUse = response.data;
        })
        .catch(error => {
          console.log(`api error:${error}`);
        });
    },
    buildingCategoryCall() {
      const buildingCategoryUrl = "api/building-category";
      VueAxios.get(buildingCategoryUrl)
        .then(response => {
          this.buildingCategory = response.data;
          // console.log(this.buildingCategory);
          // debugger;
        })
        .catch(error => {
          console.log(`api error:${error}`);
        });
    },
    buildingStructureCall() {
      const buildingStructureUrl = "api/building-structure";
      VueAxios.get(buildingStructureUrl, {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`
        }
      })
        .then(response => {
          this.buildingStructure = response.data;
        })
        .catch(error => {
          console.log(`api error:${error}`);
        });
    },
    validate() {
      const formObject = this;
      this.isValid = true;
      if (formObject.application.application_date_np !== "") {
        this.applicationDateNpValidationMessage = false;
        // this.save();
      } else {
        this.applicationDateNpValidationMessage = true;
        this.isValid = false;
      }
      if (formObject.application.building_use_id > 0) {
        this.buildingUseValidationMessage = false;
        // this.save();
      } else {
        this.buildingUseValidationMessage = true;
        this.isValid = false;
      }
      if (formObject.application.building_category_id > 0) {
        this.buildingCategoryValidationMessage = false;
        // this.save();
      } else {
        this.buildingCategoryValidationMessage = true;
        this.isValid = false;
      }
      if (formObject.application.building_structure_id > 0) {
        this.buildingStructureValidationMessage = false;
        // this.save();
      } else {
        this.buildingStructureValidationMessage = true;
        this.isValid = false;
      }
      if (formObject.application.building_length_ft !== "") {
        this.buildingLengthValidationMessage = false;
        // this.save();
      } else {
        this.buildingLengthValidationMessage = true;
        this.isValid = false;
      }
      if (formObject.application.building_breadth_ft !== "") {
        this.buildingBreadthValidationMessage = false;
        // this.save();
      } else {
        this.buildingBreadthValidationMessage = true;
        this.isValid = false;
      }
      if (formObject.application.building_height_ft !== "") {
        this.buildingHeightValidationMessage = false;
        // this.save();
      } else {
        this.buildingHeightValidationMessage = true;
        this.isValid = false;
      }
      if (formObject.application.building_storey !== "") {
        this.buildingStoreyValidationMessage = false;
        // this.save();
      } else {
        this.buildingStoreyValidationMessage = true;
        this.isValid = false;
      }
      if (formObject.application.building_max_area !== "") {
        this.buildingAreaValidationMessage = false;
        // this.save();
      } else {
        this.buildingAreaValidationMessage = true;
        this.isValid = false;
      }
      if (this.isValid) {
        this.save();
      }
    },
    scrollToTop() {
      window.scrollTo(0, 0);
    },
    save() {
      // this.validate();
      // debugger;
      if (
        this.application.id === null ||
        this.application.id === 0 ||
        this.application.id === undefined
      ) {
        const url = `/api/registration/${this.$route.params.regId}/application`;
        VueAxios.post(
          url,
          this.application,
          {
            headers: {
              "X-Requested-With": "XMLHttpRequest",
              Authorization: `Bearer ${window.localStorage.getItem("token")}`
            }
          },
          {
            timeout: 10000
          }
        )
          .then(response => {
            // debugger;
            if (response.status === 200 || response.status === 201) {
              this.application = response.data.data;
              this.successfulMessage = "Successful.";
            }
          })
          .catch(error => {
            // alert(2);
            console.log(`api error:${error}`);
          });
      } else {
        const eurl = `/api/registration/${this.$route.params.regId}/application/${this.application.id}`;
        VueAxios.put(
          eurl,
          this.application,
          {
            headers: {
              "X-Requested-With": "XMLHttpRequest",
              Authorization: `Bearer ${window.localStorage.getItem("token")}`
            },
            _method: "patch"
          },
          {}
        )
          .then(response => {
            // debugger;
            if (response.status === 200 || response.status === 201) {
              this.successfulMessage = "Data Updated successfully";
            }
            // else {
            //     this.$router.push({
            //         name: 'tabbed',
            //     });
            // }
            console.log(response);
          })
          .catch(error => {
            console.log(error);
          });
      }
    },
    edit(id) {
      // debugger;
      //    const url = `api/registration/${this.$route.params.regId}/application/${this.$route.params.id}`;
      const url = `api/registration/${this.$route.params.regId}/application/${id}`;
      VueAxios.get(url).then(response => {
        // debugger;
        this.application = response.data.data;
        console.log(response.data.data);
      });
    },
    deleterecord(id) {
      const deleteurl = `api/registration/${this.$route.params.regId}/application/${id}`;
      VueAxios.delete(deleteurl, {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`
        }
      })
        .then(response => {
          // debugger;
          if (response.status === 200 || response.status === 201) {
            this.successfulMessage = "Data deleted successfully";
          }
          console.log(response);
        })
        .catch(error => {
          console.log(error);
        });
    }
  }
};
</script>

