<template>
  <div class="">
    <v-container align-center>
      <v-row>
        <v-col>
          <v-btn :to="{ name: 'application_tab', params: { id: regId } }">Back</v-btn>
        </v-col>
      </v-row>
      <v-stepper v-model="el">
        <v-layout justify-center>
          <v-flex xs2 class="tableftrow">
            <v-card>
              <v-stepper-header ver>
                <template v-for="n in steps">
                  <v-stepper-step :key="n.sn" :step="n.sn" editable style="min-width:100%">
                    {{ n.text }}
                  </v-stepper-step>
                </template>
              </v-stepper-header>
            </v-card>
          </v-flex>
          <v-flex xs10>
            <v-card>
              <v-stepper-items>
                <v-stepper-content v-for="n in steps" :key="n.sn" :step="n.sn">
                  <component
                    :application="application"
                    :isPostAuthorized="
                      userRoleId == 2 && reg.desk_user_role_id == userRoleId && reg.phase_id == 1
                    "
                    :is="getComponent(n.sn)"
                    v-if="n.sn === el"
                    v-bind:next-step="nextStep"
                    v-bind:registration-id="regId"
                    :id="id"
                  />
                </v-stepper-content>
              </v-stepper-items>
            </v-card>
          </v-flex>
        </v-layout>
      </v-stepper>
    </v-container>
  </div>
</template>
<script>
import Application from "./components/Application.vue";
import Houseownerinfo from "./components/Houseownerinfo.vue";
import Designer from "./components/Designer.vue";
import Supervisor from "./components/Supervisor.vue";
import Landinfoaddress from "./components/Landinfoaddress.vue";
import Charkilla from "./components/Charkilla.vue";
import Landinfoarea from "./components/Landinfoarea.vue";
import Landowner from "./components/Landowner.vue";
import Contractor from "./components/Contractor.vue";
import Applicant from "./components/Applicant.vue";
// import Mason from './components/Mason.vue';
// import Consultant from './components/Consultant.vue';

const COMPONENT_MAP = {
  1: "Application",
  2: "Houseownerinfo",
  3: "Designer",
  4: "Supervisor",
  5: "Landinfoaddress",
  6: "Charkilla",
  7: "Landinfoarea",
  8: "Landowner",
  9: "Contractor",
  10: "Applicant"
  // 11: 'Mason',
  // 12: 'Consultant',
};

export default {
  name: "TabbedElements",
  props: {
    id: {
      cast: Number
    },
    regId: {
      cast: Number
    },
    sn: {
      cast: Number
    }
  },
  data() {
    return {
      el: Number(this.sn),
      userRoleId: localStorage.getItem("auth_user_role_id"),
      vertical: true,
      // : this.regId,
      // id: this.id,
      reg: [],
      steps: [
        { text: "", sn: 1 },
        { text: " ", sn: 2 },
        { text: "", sn: 3 },
        { text: "", sn: 4 },
        { text: "  ()", sn: 5 },
        { text: "", sn: 6 },
        { text: "  ()", sn: 7 },
        { text: " ", sn: 8 },
        { text: " / ", sn: 9 },
        { text: " ", sn: 10 }
      ]
      //  { text: '', sn: '11' },
      // { text: '', sn: '12' }],

      // el: 1,
      // steps: 10,
    };
  },
  components: {
    Application,
    Houseownerinfo,
    Designer,
    Supervisor,
    Landinfoaddress,
    Charkilla,
    Landinfoarea,
    Landowner,
    Contractor,
    Applicant
    //  Mason,
    // Consultant,
  },

  watch: {
    steps(val) {
      if (this.el > val) {
        this.el = val;
      }
    }
  },

  methods: {
    getComponent(n) {
      return COMPONENT_MAP[n];
    },
    nextStep(n) {
      // debugger;
      if (n === this.steps.sn) {
        this.el = 1;
      } else {
        this.el = n + 1;
      }
    }
  },
  mounted() {
    const urlApp = `api/registration/${this.$route.params.regId}`;
    this.$axios.get(urlApp).then(response => {
      this.reg = response.data.data;
    });
  }
};
</script>
<style scoped>
.vstepper {
  overflow-x: scroll;
  overflow-y: hidden;
}
.scrollbar {
  background-color: #f5f5f5;
  float: left;
  height: 300px;
  margin-bottom: 25px;
  margin-left: 22px;
  margin-top: 40px;
  width: 65px;
  overflow-x: scroll;
}

.force-overflow {
  min-height: 450px;
}
</style>
